@import "../master/base.scss";
.article-post .slick-prev {
  width: 35px !important;
}
.article-post .slick-next {
  width: 33px !important;
}
.article-new .slick-prev:before {
  background: url(../images/arr-left.png) !important;
  background-repeat: no-repeat !important;
  background-position: center !important;
}
.article-new .slick-next:before {
  background: url(../images/arr-right.png) !important;
  background-repeat: no-repeat !important;
  background-position: center !important;
}
.article-post .slick-prev:before {
  content: "" !important;
  background: url(../images/lk-left.png);
  width: 30px;
  height: 30px;
  display: block;
  background-repeat: no-repeat;
  background-position: center;
}
.article-post .slick-next:before {
  content: "" !important;
  background: url(../images/lk-right.png);
  width: 30px;
  height: 30px;
  display: block;
  background-repeat: no-repeat;
  background-position: center;
}
.article-post {
  .article-new {
    .slider-for {
      .slick-list {
        margin-left: 0px !important;
      }
      .item-slide-title {
        margin-bottom: 0px;
        font-weight: 400;
        line-height: 22px;
        padding: 12px;
        background: rgba(0, 0, 0, 0.54);
      }
    }
    article {
      .image {
        height: 280px;
        position: relative;
        overflow: hidden;
        img {
          position: absolute;
          top: 50%;
          transform: translateY(-50%);
        }
      }
    }
  }
  .article-post__wrapper {
    box-shadow: 0px 1px 15px rgba(0, 0, 0, 0.2);
    border-radius: 5px;
    padding: 20px 15px;
    h2 {
      font-size: 22px;
      font-weight: 700;
      text-transform: uppercase;
      margin-bottom: 20px;
      color: $bg-primary-color;
    }
  }
  .slick-slide {
    margin-left: 2px;
  }

  .slider-nav__info {
    transition: 0.5s;
    span {
      display: block;
    }
  }
  &__info {
    h2 {
      margin-bottom: 0;
    }

    .heading-category {
      .view-more {
        color: $bg-secondary-color;

        &:hover {
          color: $text-heading-color;
        }
      }
    }
  }

  &__slide {
    @include for-tablets {
      padding-bottom: 30px;
      margin-bottom: 20px;
      border-bottom: 1px dashed #bdbdbd;
    }
  }

  .slider.slider-nav.slick-initialized.slick-slider {
    .slick-list.draggable {
      margin: 0 35px;
    }
  }

  .slick-arrow {
    height: 100%;
    background-color: #e9e9e9;
    width: 30px;
    font-weight: 600;
  }
  .slick-prev {
    left: 0px;
    width: 30px;
    font-weight: 600;

    &:hover {
      &:before {
        color: $bg-primary-color;
      }
    }

    &:before {
      content: "<";
      color: #b1b1b1;
      font-size: 30px;
      font-weight: 600;
    }
  }
  .slick-next {
    right: 0px;

    &:hover {
      &:before {
        color: $bg-primary-color;
      }
    }

    &:before {
      content: ">";
      color: #b1b1b1;
      font-size: 30px;
      font-weight: 600;
    }
  }
}

.article-post {
  .article-feature {
    .slider-nav {
      margin-left: -10px;
      margin-right: -10px;
      .slick-dots {
        display: none;
      }
      .item {
        padding: 0px 10px;
        .title {
          h3 {
            font-size: 14px;
            font-weight: 400;
            line-height: 18px;
            text-align: justify;

            a {
              color: #000;
              transition: 0.5s;
            }
            a:hover {
              color: $bg-secondary-color;
            }
          }
        }
      }

      button.slick-prev.slick-arrow {
        z-index: 9;
        background: none;
      }
      button.slick-next.slick-arrow {
        z-index: 9;
        background: none;
      }
      .slick-list.draggable {
        margin: 0px !important;
      }
    }
    .slider-for {
      border-bottom: 1px dashed #bdbdbd;
      padding-bottom: 20px;
      margin-bottom: 20px;
      .slick-slide {
        margin-left: 0px;
        cursor: auto;
      }
      .slick-list {
        margin-left: 0px;
        .item {
          display: flex !important;
          .image {
            width: 50%;
          }
          .description {
            width: 50%;
            padding-left: 15px;
            h3 {
              font-size: 16px;
              line-height: 20px;
              margin-bottom: 5px;
              text-align: justify;
              a {
                color: #000;
                transition: 0.5s;
              }
              a:hover {
                color: $bg-secondary-color;
              }
            }
            span.date {
              font-size: 12px;
              color: #bdbdbd;
              margin-bottom: 5px;
              display: block;
              i {
                margin-right: 5px;
              }
            }
            p {
              font-size: 14px;
              font-weight: 400;
              text-align: justify;
            }
          }
        }
      }
    }
  }
}

.article-feature-slide {
  padding: 30px 0 0;

  .owl-carousel .owl-item img {
    display: block;
    // width: 100%;
    height: 120px;
    object-fit: cover;
  }

  .owl-theme .owl-nav {
    text-align: right;

    button {
      &:focus {
        outline: none;
      }

      &:hover {
        border-radius: 50%;
        background-color: $bg-primary-color;
        span {
          color: #fff;
          border: 1px solid #d6d3d3;
        }
      }

      span {
        color: #828282;
        border: 1px solid currentColor;
        width: 20px;
        height: 20px;
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 10px;
      }
    }
  }
}

.slick-slide {
  cursor: pointer;
  img {
    object-fit: cover;
    // width: 100%;
  }
}

.owl-carousel .owl-item img {
  display: block;
  // width: 100%;
  height: 125px;
  object-fit: cover;
  border: 0.5px solid #bdbdbd;
  padding: 4px;
}

.item-slide-title {
  position: absolute;
  bottom: 0;
  color: #fff;
  font-weight: bold;
  font-size: 15px;
  line-height: 18px;
  color: #ffffff;
  padding: 12px 16px;
  background-color: rgba(0, 0, 0, 0.44);
  width: 100%;
}

.slider-nav {
  &__info {
    background: #e9e9e9;
    text-align: center;
    padding: 8px 0;

    &:hover {
      background-color: $bg-primary-color;
      box-shadow: 0 5px transparent inset, 0 -5px #8f0801 inset,
        -5px 0 transparent inset, 5px 0 transparent inset;
      -webkit-box-shadow: 0 5px transparent inset, 0 -5px #8f0801 inset,
        -5px 0 transparent inset, 5px 0 transparent inset;

      .slider-nav__heading,
      .slider-nav__text {
        color: #fff;
      }
    }
  }
  &__heading {
    font-size: 28px;
    line-height: 33px;
    text-align: center;
    color: #898989;
    margin-bottom: 5px;
    text-transform: uppercase;
    font-weight: bold;
  }

  &__text {
    color: #898989;
    margin-bottom: 0;
    font-size: 15px;
    line-height: 18px;
    text-transform: uppercase;
  }

  &__anchor {
    position: relative;
  }

  & .slick-current {
    // margin-left: 0;
    .slider-nav__heading,
    .slider-nav__text {
      color: #fff;
    }

    .slider-nav__info {
      background-color: $bg-primary-color;
      box-shadow: 0 5px transparent inset, 0 -5px #8f0801 inset,
        -5px 0 transparent inset, 5px 0 transparent inset;
      -webkit-box-shadow: 0 5px transparent inset, 0 -5px #8f0801 inset,
        -5px 0 transparent inset, 5px 0 transparent inset;
    }
  }
}

/* the slides */
.slick-slide {
  margin-left: 5px;
}

/* the parent */
.slick-list {
  margin-left: -5px;
}

.article-feature {
  h4,
  p {
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 3;
    overflow: hidden;
  }

  small {
    font-size: 12px;
    color: #4f4f4f;
  }

  &__content {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;

    .active {
      color: $bg-secondary-color;
    }
  }

  &__new {
    img {
      // width: 100%;
      max-height: 150px;
      object-fit: cover;
      border: 0.5px solid #bdbdbd;
      padding: 4px;
    }
  }
}

.information {
  background-image: linear-gradient(
      0deg,
      rgba(221, 43, 35, 0.9),
      rgba(221, 43, 35, 0.9)
    ),
    url("../images/slider-image.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: bottom;
  ul {
    padding-left: 0px;
    float: left;
    width: 100%;
    li {
      float: left;
    }
  }
}

.group-banner {
  img {
    padding: 1px;
    border: 1px solid #bdbdbd;

    @include for-phone-only {
      margin-bottom: 7px;
    }
  }
}

.section-news {
  background-color: #f8f8f8;

  h2,
  h4,
  p,
  a {
    color: #000;
  }
  a:hover {
    color: $bg-secondary-color;
  }

  .group-banner {
    margin-bottom: 40px;

    @include for-phone-only {
      margin-bottom: 20px;
    }
  }

  .view-more {
    color: $bg-primary-color;
  }
}

body.compensate-for-scrollbar {
  overflow: auto;
  margin: auto;
}

.slider {
  .multiple-items {
    margin-bottom: 0px;
    button.slick-prev.slick-arrow {
      left: 25px;
      z-index: 9;
    }
    button.slick-next.slick-arrow {
      right: 25px;
      z-index: 9;
    }
    .slick-prev:before,
    .slick-next:before {
      font-size: 26px;
      // color: #666;
    }
    .slick-prev:hover:before,
    .slick-next:hover:before {
      opacity: 1;
    }
    .item {
      height: 600px;
      position: relative;
      overflow: hidden;
      img {
        position: absolute;
        // top: 50%;
        // transform: translateY(-50%);
        // width: 100%;
        height: 100%;
        bottom: 0;
      }
    }
    ul.slick-dots {
      position: absolute;
      bottom: 20px;
    }
    .slick-dots li button:before {
      font-size: 12px;
    }
    li.slick-active {
      button:before {
        color: $bg-primary-color;
      }
    }
    li {
      button:hover:before {
        color: $bg-primary-color;
        opacity: 0.75;
      }
    }
  }
}
.connect-page {
