.article-post__slide {
  .d-flex {
    margin-bottom: 20px;
  }
  .read-more {
    color: $bg-secondary-color;
  }
  .read-more:hover {
    color: $text-primary-color;
  }
}
.article-post__info {
  .article-feature {
    .slider-nav {
      .slick-arrow {
        display: block !important;
        height: 25px !important;
        transform: translate(0);
        top: auto;
        bottom: -30px;
        border: 1px solid #828282;
        right: 10px;
      }
      .slick-prev:before {
        left: 3px;
      }
      .slick-prev:before,
      .slick-next:before {
        width: 15px;
        height: 10px;
        position: absolute;
        top: 6px;
      }
      .slick-next:before {
        right: 3x;
      }
      .slick-prev:before {
        right: 2px;
      }
      button.slick-prev.slick-arrow {
        right: 40px !important;
        left: auto;
      }
      button {
        border-radius: 50%;
        padding: 5px;
        width: 25px !important;
      }
      // button:hover {
      //   border-color: $bg-secondary-color;
      // }
    }
  }
  .d-flex {
    margin-bottom: 20px;
  }

  h2.heading-featured {
    margin-bottom: 0px;
  }
  .article-feature {
    .slider {
      button {
        display: none !important;
      }
    }
    .slick-dots {
      display: none !important;
    }
  }
  .slider-nav {
    article {
      .image {
        width: 50%;
        float: left;
        padding-right: 10px;
      }
      .title {
        width: 50%;
        float: left;
      }
    }
  }
}
