@import "base.scss";

.footer {
  padding: 0px;

  .connect-bottom {
    margin-top: 0px;
    padding: 25px 0px;
    border-top: 1px solid #dadada;
    h2 {
      border: none;
    }
    .slide-vertical {
      .slick-prev {
        left: 0px;
        z-index: 9;
        opacity: 0.7;
      }
      .slick-next {
        right: 0px;
        z-index: 9;
        opacity: 0.7;
      }
      .slick-prev:hover,
      .slick-next:hover {
        opacity: 1;
      }
    }
  }
  ul {
    list-style: none;
    padding-left: 0px;
  }
  .heading {
    border-bottom: 1px solid #bdbdbd;
    margin-bottom: 15px;
    h2 {
      font-weight: 700;
      font-size: 18px;
      color: #000;
      text-transform: uppercase;
      line-height: 24px;
      display: inline-block;
      // border-bottom: 2px solid $bg-primary-color;
      margin-bottom: -1px;
      padding-bottom: 8px;
      position: relative;
    }
    h2:before {
      content: "";
      position: absolute;
      left: 0;
      bottom: 0;
      width: 94px;
      height: 3px;
      background: $bg-primary-color;
      margin-bottom: -1px;
    }
  }
  .footer__info {
    border-top: 3px solid $bg-primary-color;
    padding: 50px 0px;
    h3 {
      font-size: 18px;
      font-weight: 700;
      color: $bg-primary-color;
      margin-bottom: 15px;
    }
    .footer__contact-list {
      li {
        color: #000;
        font-size: 14px;
        line-height: 24px;
        margin-bottom: 2px;
        a {
          color: #000;
          &.link {
            color: $bg-primary-color;
          }
        }
        a:hover {
          color: $bg-primary-color;
        }
      }
    }
  }
  .list-item {
    margin-bottom: 0px;
    li {
      margin-bottom: 2px;
      position: relative;
      padding-left: 22px;
      a {
        font-size: 14px;
        color: #000;
        line-height: 26px;
        transition: 0.5s;
      }
      a:hover {
        color: $bg-primary-color;
      }
    }
    li:before {
      content: "";
      display: block;
      position: absolute;
      top: 6px;
      left: 0;
      width: 15px;
      height: 15px;
      background: url(../images/icon-item.png);
      background-repeat: no-repeat;
      background-position: center;
    }
  }

  a {
    color: #00689b;
  }

  &__info {
    background-image: url("../images/bgfoot.png");
  }

  &__logo {
    @include for-phone-only {
      margin-bottom: 10px;
    }
  }

  &__copyright {
    background-color: #db251c;
    padding: 10px 0;
    p {
      font-size: 14px;
    }
    a {
      color: #fff;
    }
    .icon {
      img {
        width: 20px;
      }
    }

    @include for-phone-only {
      font-size: 12px;
    }

    @include for-small-phone {
      font-size: 10px;
    }
  }

  &__copyright-content {
    display: flex;
    color: #fff;
    align-items: center;

    p {
      @media only screen and (max-width: 360px) {
        font-size: 8px;
      }
    }
  }

  @include for-small-desktops {
    padding: 30px 0px 0;
  }

  .tablet-col {
    @include for-tablets {
      margin-bottom: 15px;
    }
  }
}

.statisticl {
  display: flex;
  justify-content: flex-end;
  margin-left: auto;
  margin-bottom: 0;

  &-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 4px 0px;
    font-weight: 600;

    &:not(:last-child) {
      margin-right: 39.85px;
    }
  }

  &-item__info {
    display: flex;
    align-items: center;
  }

  p {
    margin-bottom: 0;
  }

  i {
    margin-right: 8px;
  }
  .statisticl-item__total .statisticl__text {
    margin-left: 10px !important;
  }
}

.connect-bottom {
  h2 {
    font-size: 18px;
    font-weight: 700;
    text-transform: uppercase;
    color: #000;
  }
  .slick-prev:before,
  .slick-next:before {
    color: #828282;
  }
}
.notification__content {
  .statisticl-item {
    .statisticl__text,
    .statisticl__text {
      color: #fff;
      font-weight: 400;
    }
  }
}
