// globals
@import "includes/master/reset.scss";
@import "includes/master/font.scss";
@import "includes/master/base";
@import "includes/master/dynamic-content.scss";

// masterpage
@import "includes/master/header.scss";
@import "includes/master/footer";
@import "includes/master/aside.scss";
@import "includes/page/homePage.scss";
img {
  width: 100%;
}
// pages
.heading-2 {
  border-bottom: 2px #dc2f27 solid;
  position: relative;
  overflow: hidden;
  margin-bottom: 10px !important;
  font-size: 14px;
  text-transform: uppercase;
  letter-spacing: 1px;
  span {
    background: #dc2f27;
    display: inline-block;
    padding: 8px 15px;
    color: #fff;
    position: relative;
    padding-right: 28px;
    padding-bottom: 5px;
  }
  span:before {
    content: "";
    width: 0;
    height: 0;
    border-right: 26px solid #e94336;
    border-bottom: 12px solid #e94336;
    position: absolute;
    top: 14px;
    bottom: 0;
    right: -16px;
    transform: rotate(59deg);
    z-index: 1;
    border-top: 0px solid transparent;
    border-left: 8px solid transparent;
  }
  span:after {
    content: "";
    width: 0;
    height: 0;
    border-right: 18px solid #fff;
    border-bottom: 32px solid transparent;
    position: absolute;
    top: 0px;
    bottom: 0;
    right: 0px;
  }
}
.heading-2 {
  @include for-small-desktops {
    font-size: 16px;
  }

  @include for-tablets {
    font-size: 18px;
    line-height: 1.2;
  }

  @include for-small-phone {
    font-size: 16px;
  }

  @media only screen and (max-width: 360px) {
    font-size: 14px;
  }
}
.heading-3 {
  font-style: normal;
  font-weight: bold;
  font-size: 20px;
  line-height: 23px;
  color: $bg-primary-color--active;

  @include for-tablets {
    font-size: 16px;
  }

  @include for-phone-only {
    font-size: 12px;
  }

  @include for-small-phone {
    font-size: 11px;
    line-height: 1.2;
  }

  @media only screen and (max-width: 360px) {
    font-size: 10px;
  }

  @media only screen and (max-width: 320px) {
    font-size: 9px;
  }
}
.heading-4 {
  color: $text-heading-color;
  font-size: 16px;
  line-height: 19px;
  font-weight: bold;
  margin-bottom: 0;
  transition: all 0.2s ease-out;

  &--post {
    color: #000000;
    font-size: 15px;

    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 6;
    overflow: hidden;

    @include for-tablets {
      font-size: 15px;
    }
  }

  &--header {
    color: $bg-secondary-color;

    @include for-tablets {
      font-size: 14px;
    }

    @include for-phone-only {
      font-size: 12px;
    }

    @media only screen and (max-width: 360px) {
      font-size: 11px;
    }

    @media only screen and (max-width: 360px) {
      font-size: 10px;
    }

    &.active {
      color: $bg-secondary-color;
    }
  }

  &--white {
    font-size: 14px;
    color: #fff;
    font-weight: 500;
    line-height: 21px;

    @include for-tablets {
      font-weight: 600;
    }
  }

  &--two-line {
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    overflow: hidden;
  }
}
.heading-5 {
  display: inline-block;
  color: #fff;
  background-color: $bg-primary-color;
  font-size: 14px;
  line-height: 14px;
  font-weight: 600;
  padding: 8px 25px 8px 15px;
  margin-bottom: 0;
  position: relative;

  &:before {
    content: "";
    width: 0;
    height: 0;
    border-right: 25px solid $text-link-aside;
    border-bottom: 10px solid $text-link-aside;
    position: absolute;
    top: 14px;
    bottom: 0;
    right: -14px;
    transform: rotate(58deg);
    z-index: 1;
    border-top: 0px solid transparent;
    border-left: 6px solid transparent;
  }

  &:after {
    content: "";
    width: 0;
    height: 0;
    border-right: 18px solid #fff;
    border-bottom: 29px solid transparent;
    position: absolute;
    top: 0px;
    bottom: 0;
    right: 0px;
  }
}
.form-control:focus {
  outline: 0;
  box-shadow: none;
}
.nav-breadcrumb {
  display: block;
  width: 100%;
  ol {
    border-radius: 0px;
    padding: 8px 12px;
    border-left: 5px solid $bg-primary-color;
    display: block;
    li {
      display: inline-block;
      text-transform: capitalize;
      a,
      span {
        font-size: 13px;
        font-weight: 500;
      }
      a {
        color: #333;
        transition: 0.5s;
      }
      a:hover {
        color: $bg-primary-color;
      }
      span {
        color: #333;
      }
    }
    .active {
      span {
        color: $bg-primary-color;
      }
    }
  }
  .breadcrumb-item + .breadcrumb-item {
    padding-left: 20px;
    position: relative;
  }
  .breadcrumb-item + .breadcrumb-item::before {
    position: absolute;
    background: url(../images/icon-breadcrumb.png);
    width: 8px;
    height: 9px;
    background-repeat: no-repeat;
    left: 5px;
    background-size: cover;
    content: "";
    top: 6px;
  }
}
.heading-category {
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #fff;
  position: relative;
  padding-bottom: 15px;

  a {
    color: #fff;
  }

  &:after {
    content: "";
    width: 100%;
    height: 5px;
    position: absolute;
    left: 0;
    bottom: -2px;
    display: block;
    background-image: url("../images/border-heading.png");
    background-repeat: no-repeat;
    background-position: left bottom;
  }

  &--border {
    border-bottom: 1px solid #bdbdbd;
    color: #000;
    padding-bottom: 10px;

    &:after {
      width: 25%;
      height: 3px;

      background-image: none;
      background-color: $text-heading-color;
    }
  }

  .view-more {
    transition: all 0.2s ease-out;

    &:hover {
      color: $bg-secondary-color;
    }

    @include for-small-desktops {
      font-size: 12px;
    }
  }

  @include for-tablets {
    padding-bottom: 10px;
    margin-bottom: 15px !important;
  }
}
.pagination-wrap {
  .pagination {
    justify-content: center;
    margin-bottom: 30px;

    @include for-phone-only {
      flex-wrap: wrap;
      justify-content: flex-start;
    }

    @include for-phone-only {
      margin-bottom: 30px;
    }

    .page-item {
      margin-right: 5px;

      @include for-phone-only {
        margin-bottom: 5px;
      }

      .page-link {
        border: 1px solid #828282;
        color: #000;
        transition: 0.5s;

        &:hover {
          color: #fff;
          background-color: $bg-primary-color;
          border-color: $bg-primary-color;
        }

        @include for-phone-only {
          padding: 0.5rem 0.7rem;
        }
      }

      &.active {
        .page-link {
          color: #fff;
          background-color: $bg-primary-color;
          border-color: $bg-primary-color;
        }
      }
    }
  }
}
.category-text {
  &__item {
    display: flex;
    align-items: top;
    justify-content: space-between;
    margin-bottom: 15px;
  }
  &__content {
    flex: 0 0 75%;
    padding-left: 15px;
  }
  &__time {
    background: #ffffff;
    border: 1px solid #e0e0e0;
    box-sizing: border-box;
    border-radius: 5px;
    flex: 0 0 20%;
    text-align: center;
    margin-right: 0px;

    &--primary {
      border: 1px solid $bg-primary-color;
    }

    @include for-small-desktops {
      flex: 0 0 25%;
    }
  }

  &__day {
    font-weight: bold;
    font-size: 22px;
    line-height: 20px;

    color: #4f4f4f;
    padding: 8px 4px;
    margin-bottom: 0;

    &--primary {
      color: $bg-primary-color;
    }
  }

  &__month {
    font-size: 15px;
    line-height: 20px;
    color: #4f4f4f;
    margin-bottom: 0;
    background-color: #e0e0e0;
    padding: 4px;
    border-radius: 0px 0px 5px 5px;

    &--primary {
      background-color: $bg-primary-color;
    }
  }
}
.category-post {
  &__item {
    display: flex;
    margin-bottom: 30px;
  }

  &__image {
    flex: 0 0 40%;
    margin-right: 20px;

    @include for-phone-only {
      margin-right: 10px;
    }

    @include for-small-phone {
      flex: 0 0 45%;
    }

    img {
      width: 100%;
      height: 120px;
      border: 0.5px solid #bdbdbd;
      padding: 3px;
      object-fit: cover;

      @include for-small-desktops {
        height: 100px;
      }

      @include for-landscape-phone {
        height: 150px;
      }

      @include for-small-phone {
        height: 130px;
      }
    }
  }
}
.category-document {
  a {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 16px 22px;
    border: 1.3px solid #ffffff;
    border-radius: 5px;
    color: #fff;

    &:hover {
      border: 1.3px solid $bg-secondary-color;
      color: $bg-secondary-color;
    }
  }

  &__item {
    margin-bottom: 24px;
  }

  &__text {
    margin-bottom: 0;
    font-size: 13px;
    line-height: 18px;
    text-transform: uppercase;
  }
}

.post-news-event {
  display: flex;
  margin-bottom: 20px;
  .box-date {
    flex: 0 0 10%;
    text-align: center;
    padding-right: 5px;
    .day {
      background-color: #ffffff;
      border: 1px solid $bg-primary-color;
      border-radius: 4px 4px 0px 0px;
      font-size: 22px;
      color: $bg-primary-color;
    }
    .month {
      font-size: 15px;
      color: #fff;
      line-height: 22px;
      font-weight: 400;
      background-color: $bg-primary-color;
      border-radius: 0px 0px 4px 4px;
    }
    span {
      display: block;
      padding: 5px;
    }
  }

  .post-news-event__image {
    flex: 0 0 30%;
    margin-right: 15px;
    border: 1px solid #bdbdbd;
    padding: 1px;
    .box {
      position: relative;
      overflow: hidden;
      height: 160px;
      img {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        width: 100%;
      }
    }

    @include for-phone-only {
      flex: 0 0 80%;
      margin-right: 0px;
    }

    @include for-small-phone {
      flex: 0 0 75%;
    }
  }

  .post-news-event__post {
    flex-grow: 1;

    .title {
      font-size: 14px;
      line-height: 22px;
      text-align: justify;
      a {
        transition: 0.5s;
        color: #000;
      }
    }
    .title:hover {
      a {
        color: $bg-secondary-color;
      }
    }
    .date {
      font-size: 12px;
      color: #bdbdbd;
      font-weight: 400;
      margin-bottom: 8px;
      i {
        margin-right: 5px;
      }
    }
    @include for-phone-only {
      margin-top: 10px;
      flex: 0 0 80%;
      margin-left: auto;
    }

    @include for-small-phone {
      flex: 0 0 75%;
    }
  }

  .post-news-event__text {
    color: #2b2b2b;
    font-size: 14px;
    line-height: 20px;
    text-align: justify;
    @include blockEllipsis(5);

    @include for-phone-only {
      @include blockEllipsis(3);
    }

    @include for-small-phone {
      // font-size: 12px;
    }
  }

  .view-more {
    color: $text-heading-color;
    transition: 0.5s;
    span {
      letter-spacing: 2px;
      margin-left: 5px;
    }

    &:hover {
      color: $bg-secondary-color;
    }
  }

  .category-text {
    &__time {
      margin-right: 10px;
      border: 1px solid $bg-primary-color;
    }

    &__day {
      color: $bg-primary-color;
    }

    &__month {
      background-color: $bg-primary-color;
      color: #fff;
    }
  }

  @include for-phone-only {
    flex-wrap: wrap;
    margin-bottom: 25px;
  }
}
.table-content {
  overflow-x: scroll;

  /* Hide scrollbar for Chrome, Safari and Opera */
  &::-webkit-scrollbar {
    display: none;
  }

  /* Hide scrollbar for IE and Edge */
  & {
    -ms-overflow-style: none;
  }

  .table {
    text-align: center;
    overflow: hidden;
    margin-bottom: 30px;

    tbody {
      a {
        color: #000;

        &:hover {
          color: $bg-secondary-color;
        }
      }
    }
  }
}
.h-title {
  border-bottom: 1px solid #bdbdbd;
  margin-bottom: 20px;
  h2 {
    font-weight: 700;
    font-size: 18px;
    color: #000;
    text-transform: uppercase;
    line-height: 24px;
    display: inline-block;
    position: relative;
    margin-bottom: -1px;
    padding-bottom: 8px;
  }
  h2:before {
    content: "";
    background: $bg-primary-color;
    width: 94px;
    height: 3px;
    position: absolute;
    left: 0;
    bottom: -1px;
  }
}
a.read-more {
  transition: 0.5s;
  font-size: 12px;
  span {
    margin-left: 6px;
    letter-spacing: 3px;
  }
}
#to-top {
  display: inline-block;
  background-color: $bg-secondary-color;
  width: 40px;
  height: 40px;
  text-align: center;
  border-radius: 4px;
  position: fixed;
  bottom: 30px;
  right: 30px;
  transition: background-color 0.3s, opacity 0.5s, visibility 0.5s;
  opacity: 0;
  visibility: hidden;
  z-index: 1000;
  display: flex;
  align-items: center;
  i {
    font-size: 16px;
    color: #fff;
    width: 40px;
  }
}
#to-top:hover {
  cursor: pointer;
  background-color: #c41f18;
}
#to-top:active {
  background-color: #c41f18;
}
#to-top.show {
  opacity: 1;
  visibility: visible;
}
.list-social {
  background: #e9ecef;
  padding: 10px 0px !important;
  margin-top: 30px;
  ul {
    padding-left: 0px;
    list-style: none;
    margin-bottom: 0px;
    li {
      display: inline-block;
      margin-right: 5px;
      a {
        // background: $bg-primary-color;
        color: #2b2b2b;
        padding: 8px 12px;
        border-radius: 4px;
        font-weight: 400;
        font-size: 13px;
        transition: 0.5s;
        text-transform: capitalize;
        img {
          margin-right: 5px;
          width: auto !important;
        }
      }
      a:hover {
        // background: $bg-secondary-color;
        color: $bg-primary-color;
      }
    }
  }
}
#detail {
  header {
    margin-bottom: 15px;
    .title {
      font-weight: 700;
      font-size: 16px;
      color: #dc2f27;
      line-height: 22px;
      margin-bottom: 10px;
    }
    .date {
      font-size: 12px;
      color: #bdbdbd;
      font-weight: 400;
      margin-bottom: 0px;
      i {
        margin-right: 5px;
      }
    }
    .time {
      font-size: 12px;
      color: #bdbdbd;
      margin-left: 5px;
    }
  }
  article {
    figure {
      margin-bottom: 20px;
    }
    figcaption {
      margin-top: 10px;
      text-align: center;
      font-style: italic;
    }
  }
  .author {
    font-size: 14px;
    color: #000000;
    line-height: 22px;
    font-style: italic;
  }
  ul {
    li {
      margin-bottom: 10px;
      font-size: 14px;
      line-height: 20px;
      text-align: justify;
      color: #000;
    }
  }
}
.list-news {
  padding-top: 50px;
  .title {
    margin-bottom: 20px;
    border-bottom: 1px solid #bdbdbd;

    h2 {
      display: inline-block;
      position: relative;
      font-size: 16px;
      color: $bg-primary-color;
      padding-bottom: 8px;
      text-transform: uppercase;
      margin-bottom: -1px;
      font-weight: 700;
    }
    h2:before {
      content: "";
      position: absolute;
      left: 0px;
      bottom: -1px;
      height: 3px;
      background: $bg-primary-color;
      width: 90px;
    }
  }
  ul {
    list-style: none;
    padding-left: 0px;
    li {
      margin-bottom: 10px;
      padding-left: 20px;
      text-align: justify;
      a {
        transition: 0.5s;
        position: relative;
        color: #000;
      }
      a:hover {
        color: $bg-primary-color;
      }
      a:before {
        content: "";
        display: block;
        position: absolute;
        top: 2px;
        left: -20px;
        width: 15px;
        height: 15px;
        background: url(../images/icon-item.png);
        background-repeat: no-repeat;
        background-position: center;
      }
    }
  }
}
.contact {
  padding-top: 20px;
  form {
    .form-group {
      .input-group-prepend {
        width: 36px;
        text-align: center;
        .input-group-text {
          i {
            font-size: 14px;
          }
        }
      }
    }
    .form-control {
      font-size: 13px;
      color: $text-primary-color;
    }
    .box-bottom {
      button {
        background-color: $bg-primary-color;
        color: #fff;
        font-size: 14px;
        border-color: $bg-primary-color;
        min-height: 35px;
        font-weight: 400;
      }
      button:focus {
        outline: none;
        box-shadow: none;
      }
      button:hover {
        color: #fff;
        background-color: $bg-secondary-color;
        border-color: $bg-secondary-color;
      }
    }
  }
  fieldset {
    border-radius: 4px;
    border: 1px solid silver;
    margin: 0 2px;
    padding: 0.35em 0.625em 0.75em;
    legend {
      margin-bottom: 0;
      font-size: 14px;
      font-weight: 500;
      line-height: 16px;
      color: $text-primary-color;
      display: inline-block;
      width: auto;
      padding: 0px 10px;
    }
    .box-text {
      padding: 0.5rem !important;
      h2 {
        font-size: 16px;
        color: $bg-primary-color;
        line-height: 22px;
        font-weight: bold;
        margin-bottom: 15px;
      }
      ul {
        padding-left: 0px;
        list-style: none;
        color: $text-primary-color;
        margin-bottom: 0;
        li {
          color: $text-primary-color;
          line-height: 22px;
          font-size: 14px;
          // text-align: justify;
          margin-bottom: 10px;
          i {
            width: 25px;
            color: $text-primary-color;
          }
        }
        li:last-child {
          margin-bottom: 0px;
        }
      }
    }
    .box {
      padding: 6px;
    }
  }
  .contact-map {
    padding-top: 20px;
    .box-text {
      border: 1px solid silver;
      border-radius: 4px;
    }
    iframe {
      border-radius: 4px;
      margin-bottom: -5px;
    }
  }
}
.list-lecture {
  h1 {
    font-size: 18px;
    color: $bg-primary-color;
    text-transform: uppercase;
    font-weight: bold;
    margin-bottom: 20px;
    margin-top: 10px;
  }
  table {
    border: none;
    border-radius: 6px;
    border-bottom: 3px solid $bg-primary-color !important;
  }
  thead {
    background-color: $bg-primary-color;
    border-radius: 6px 6px 0px 0px;
    tr {
      border-radius: 6px;
    }
    th {
      color: #fff;
      font-weight: 400;
      border-radius: 6px 6px 0px 0px;
      border: none;
      border-right: 1px solid #fff;
      font-size: 14px;
    }
    th:last-child {
      border-right: none;
    }
  }
  tbody {
    tr:nth-of-type(odd) {
      background: #fff;
    }
    tr:nth-of-type(even) {
      background: #fdf1f1;
    }
    tr {
      th,
      td {
        border: none;
        border-right: 1px solid #f4b2af;
      }
      th:last-child,
      td:last-child {
        border-right: transparent;
      }
    }
  }
  h3,
  h6 {
    font-size: 13px;
    font-weight: 400;
    line-height: 18px;
    margin: 0px;
  }
  a {
    transition: 0.5s;
    color: $bg-primary-color;
  }
  a:hover {
    color: #000;
  }
  th {
    font-size: 13px;
  }
}
.lecture-item-content {
  header {
    h1 {
      font-size: 16px;
      color: $bg-primary-color;
      line-height: 22px;
      font-weight: 700;
      margin-bottom: 20px;
      text-transform: initial;
    }
  }
  table {
    td,
    th {
      font-size: 14px;
      line-height: 20px;
    }
    th {
      width: 20%;
    }
  }
  iframe {
    border: none;
    padding-top: 15px;
  }
  .box-text {
    p {
      margin-bottom: 0px;
    }
    span {
      color: #f24822;
    }
    i {
      color: #333;
      width: 15px;
      text-align: center;
    }
  }
}

.question {
  .form-question {
    border-bottom: 1px dashed #828282;
    margin-bottom: 40px !important;
    padding-bottom: 40px !important;
    fieldset,
    .box-bottom {
      width: 80%;
      margin: auto;
    }
  }
  .card {
    margin-bottom: 30px;
    .card-header {
      padding: 10px 20px;
