        margin-bottom: 0px;
      }
      article {
        .image {
          height: 60px;
          position: relative;
          overflow: hidden;
          border: 1px solid #bdbdbd;
          img {
            position: absolute;
            top: 50%;
            padding: 1px;
            transform: translateY(-50%);
            height: 100%;
          }
        }
        .image:before {
          content: "";
          position: absolute;
          top: 0;
          height: 1px;
          width: 100%;
          background: #fff;
          z-index: 9;
        }
        .image:after {
          content: "";
          position: absolute;
          bottom: 0;
          height: 1px;
          width: 100%;
          background: #fff;
          z-index: 9;
        }
      }
    }
  }
