@import "mixins";
@import "base";

header {
  .header-head {
    padding: 10px 0px;
    border-top: 2px solid #dc2f27;
    background: #ffffff url(../images/header.png);
    background-repeat: no-repeat;
    background-position: top;
    .logo {
      img {
        width: 130px;
      }
    }
    .header-head__info {
      p {
        font-size: 14px;
        color: $text-primary-color;
        margin-bottom: 5px;
        font-weight: 500;
      }
      h3 {
        color: $text-heading-color;
        font-size: 20px;
        line-height: 22px;
      }
      h4 {
        color: $text-link-color;
        font-size: 16px;
      }
    }
    .header-head__info {
      padding-left: 40px;
    }
    .header-head__celebrate {
      padding-left: 80px;
      img {
        width: 100px;
      }
    }
    .box {
      position: relative;
      .header__box-login {
        position: absolute;
        right: 30px;
        top: -10px;
        background: #db251c;
        ul {
          margin: 0px;
          padding: 0px;
          li {
            display: inline-block;
            margin: 0px 2px;
            img {
              width: 30px;
              height: 18px;
              border: 1px solid #f1f1f1;
              border-radius: 2px;
            }
          }
        }
        .header__form {
          font-size: 13px;
          padding: 5px 10px;
          //   background: url(../images/bg-login.png);
          //   background-position: center;
          //   background-size: contain;
          //   background-repeat: no-repeat;
          a {
            color: #fff;
            font-size: 13px;
            letter-spacing: 1px;
          }
        }
        .header__form::before {
          content: "";
          display: block;
          position: absolute;
          left: -26px;
          top: 0;
          background: url(../images/flag/before.jpg);
          width: 27px;
          height: 28px;
        }
        .header__form::after {
          content: "";
          display: block;
          position: absolute;
          right: -24px;
          top: 0;
          background: url(../images/flag/after.jpg);
          width: 32px;
          height: 28px;
        }
      }
    }
  }
  nav {
    background: $bg-primary-color;
    padding: 0px !important;
    #main-menu {
      ul {
        li {
          a {
            color: #fff;
            font-size: 14px;
            text-transform: uppercase;
            font-weight: 500;
            padding: 12px 10px;
            transition: 0.5s;
          }
          a:hover {
            background-color: $bg-primary-color--active;
            color: #fff;
          }
        }
        .active > a {
          background-color: $bg-primary-color--active;
          color: #fff;
        }
      }
      .dropdown-menu {
        background: $bg-primary-color;
        width: 220px;
        border-radius: 0px;
        border: none;
        margin: 0px;
        padding: 0px;
        li {
          a {
            border-bottom: 1px solid #f1f1f1;
            font-weight: 400;
            transition: 0.5s;
          }
        }
        li:last-child {
          a {
            border-bottom: none;
          }
        }
        .dropdown-item:hover {
          background-color: $bg-primary-color--active;
          color: #fff;
        }
        .dropdown-submenu {
          position: relative;
          .dropdown-menu {
            top: 0;
            left: 100%;
            margin-left: 0rem;
            margin-right: 0rem;

            li {
              a {
                border-bottom: 1px solid #f1f1f1;
                font-weight: 400;
                transition: 0.5s;
              }
            }
            li:last-child {
              a {
                border-bottom: none !important;
              }
            }
          }
        }
      }
      // li.nav-item.dropdown:hover,
      // li.dropdown-submenu:hover {
      //   > ul.dropdown-menu {
      //     display: block;
      //   }
      // }
      li.dropdown-submenu {
        .dropdown-toggle::after {
          right: 15px;
          position: absolute;
          top: 50%;
          transform: translateY(-50%);
        }
      }
      form {
        background: #fff;
        border-radius: 4px;
        input {
          font-size: 14px;
          cursor: auto;
          color: $text-primary-color;
          border-radius: 4px 0px 0px 4px;
          border: none;
          margin-right: 0px !important;
          background: #fff;
        }
        input::placeholder {
          font-size: 12px;
          color: #555555;
        }
        button {
          border-radius: 0px 4px 4px 0px;
          border: none;
          outline: none;
          color: #555555;
          font-size: 14px;
          transition: 0.5s;
          background: #ffffff;
        }
        button:hover {
          background: #ffffff;
          color: $bg-primary-color;
        }
      }
    }
  }
}
