@import "mixins";
@import url("https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,400;0,500;0,700;1,400;1,500;1,700&display=swap");
:root {
  --main-color: red;
  --title-color: #1a1a1a;
  // --hover-color: #ff9800;
  --hover-color: #fcc133;
}

$text-primary-color: #2b2b2b;

$text-link-color: #e57e22;
$text-link-aside: #00689b;

$text-heading-color: #dd231e;

$bg-primary-color: #dc2f27;
$bg-secondary-color: #fcc133;
$bg-primary-color--active: #fcc133;
$bg-active-color: #038ed3;

html {
  overflow-x: hidden;
}

body {
  font-size: 14px;
  line-height: 1.4;
  color: $text-primary-color;
  overflow-x: hidden;
  font-family: "Roboto", sans-serif !important;
}

@media screen and (min-width: 1200px) {
  .container {
    max-width: 1200px;
    margin: 0 auto;
  }
}

.article-post,
.information,
.section-news,
.mutilmedia,
.connect-bottom {
  padding: 40px 0;

  @include for-small-desktops {
    padding: 20px 0;
  }
}

h1,
h2,
h3 {
  margin-top: 0px;
}
a:hover {
  text-decoration: none;
  color: $bg-secondary-color;

  h4 {
    color: $bg-secondary-color;
  }
}

p {
  margin-bottom: 10px;
  font-size: 14px;
  line-height: 22px;
  text-align: justify;
  color: #000;
  @include for-small-phone {
    // font-size: 10px;
  }
}

// img {
