@import "mixins";
@import "base";

aside {
  margin-bottom: 30px;
  &.banner {
    .intro__item {
      height: 55px;
      margin-bottom: 10px;
      position: relative;
      overflow: hidden;
      img {
        // min-height: 50px;
        height: 100%;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        width: 100%;
      }
    }
  }
  .aside-title {
    border-bottom: 1px solid #bdbdbd;
    margin-bottom: 20px;
    h4 {
      display: inline-block;
      position: relative;
      font-size: 14px;
      color: #dc2f27;
      font-weight: bold;
      text-transform: uppercase;
      margin-bottom: -1px;
      padding-bottom: 10px;
    }
    h4::before {
      position: absolute;
      left: 0;
      bottom: 0;
      content: "";
      width: 70px;
      height: 3px;
      bottom: -1px;
      background: $bg-primary-color;
    }
    .read-more {
      color: #d83024;
    }
    .read-more:hover {
      color: $bg-secondary-color;
    }
  }
  ul {
    padding-bottom: 0px;
    margin-bottom: 0px;
    padding-left: 0px;
  }
  .list-post {
    .item {
      margin-bottom: 10px;
      .title {
        width: 100%;
        padding-left: 0px;
        h3 {
          font-weight: 400;
          margin-bottom: 0px;
          font-size: 14px;
          line-height: 20px;
          margin-bottom: 0px;
          position: relative;
          padding-left: 20px;
          a {
            transition: 0.5s;
            color: #000;
          }
        }
        h3:hover {
          a {
            color: $bg-primary-color;
          }
        }
        h3:before {
          content: "";
          display: block;
          position: absolute;
          top: 2px;
          left: 0;
          width: 15px;
          height: 15px;
          background: url(../images/icon-item.png);
          background-repeat: no-repeat;
          background-position: center;
        }
      }
      h6.date {
        font-size: 12px;
        color: #bdbdbd;
        font-weight: 400;
        margin-bottom: 0px;
        i {
          margin-right: 5px;
        }
      }
    }
  }
  &.aside-featured {
    .list-post {
      .item {
        .image {
          width: 35%;
        }
        .title {
          width: 65%;
          padding-left: 10px;
          h3 {
            line-height: 18px;
            padding-left: 0px;
          }
          h3:before {
            display: none;
          }
          a {
            padding-left: 0px;
          }
        }
      }
      .item:first-child {
        border-bottom: 1px dashed #626262;
        padding-bottom: 10px;
        margin-bottom: 10px;
        display: block !important;
        .image,
        .title {
          width: 100% !important;
        }
        .title {
          padding-left: 0px;
          margin-top: 10px;
          h3 {
            font-weight: 600;
          }
        }
      }
    }
  }
}
